import React, { Component } from "react";
import styled from "styled-components";
import profile from "./profile.jpg";

const StyledInfo = styled.div`
  display: flex;
`;

const Container = styled.span`
  padding-right: 20px;
`;

const Pic = styled.img`
  border-radius: 50%;
  height: 100px;
`;

const Title = styled.div`
  color: #eef3f9;
  font-size: 1.1em;
  font-weight: 800;
`;

const SubTitle = styled.div`
  color: #cfdaeb;
  font-weight: 300;
`;

const EmSubTitle = SubTitle.withComponent("em");

class Info extends Component {
  render() {
    return (
      <StyledInfo className={this.props.className}>
        <Container>
          <Pic src={profile} alt="Profile" />
        </Container>
        <div>
          <Title>Danny Jones</Title>
          <SubTitle>Full Stack Engineer</SubTitle>
          <EmSubTitle>Ruby | PostgreSQL | React</EmSubTitle>
        </div>
      </StyledInfo>
    );
  }
}

export default Info;



